<template>
   <div 
        class="column"
        @drop="dropTaskOrColumn($event, column.tasks, columnIndex)" 
        @dragover.prevent
        @dragenter.prevent
        draggable
        @dragstart.self="pickupColumn($event, columnIndex)"> 
          <div class="heading-wrapper">
            <h4>{{ column.name }}</h4>
            <button @click="deleteColumn(columnIndex, column.name)"
            class="delete-col"
            >X</button>
          </div>
          <!-- .self modifier tahaj LEN column, nie jeho child componenty -->
          <input type="text" 
          placeholder="+ Add New Task" 
          class="add-task-input" 
          @keyup.enter="addTask($event, column.tasks)">  <!-- $event - defaultna hodnota z inputu -->
            <TaskItem 
                v-for="(task, taskIndex) in column.tasks" 
                :board="board"
                :column="column"
                :task="task"
                :taskIndex="taskIndex" 
                :columnIndex="columnIndex"
                :key="taskIndex"
            />
      </div>
</template>

<script>
import TaskItem from '@/components/TaskItem'
import movingTaskOrColumnMixin from "@/mixins/movingTaskOrColumnMixin.js"

export default {
    components: { TaskItem },
    mixins: [ movingTaskOrColumnMixin ],

    methods: {
        addTask(event, taskList) {
            this.$store.commit('CREATE_TASK', {
            taskList,
            name: event.target.value
            })
            event.target.value = ''
        },
      
        pickupColumn(e, colIndex ) {
            e.dataTransfer.effectAllowed = 'move'
            e.dataTransfer.dropEffect = 'move'

            e.dataTransfer.setData('column-index', colIndex)
            e.dataTransfer.setData('type', 'column')
        },

        deleteColumn(columnIndex, columnName) {
            const confirmation = confirm('Do you want to delete "' + columnName + '" column?')
            
            if(confirmation)
                this.$store.commit('DELETE_COLUMN', columnIndex)
        }
    }
}
</script>

<style>
    .column {
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    width: 350px;
    background-color: #F18F01;
    box-shadow: 0 2px 2px 0px grey;
    border-radius: 5px;
    margin-bottom: 2rem;
    }
    .delete-col:hover {
        cursor: pointer;
        background-color: rgba(0,0,0, .1);
    }
    .delete-col {
        display: inline-block;
        align-self: center;
        background-color: transparent;
        border: none;
    }
    .add-task-input::placeholder {
    color: black
    }
    .add-task-input:focus,
    .add-task-input:active {
    background-color: rgba(0, 0, 0, .1)
    }
    .add-task-input {
    outline: none;
    margin-bottom: 1rem;
    padding: .5em;
    background: transparent;
    border: none;
    border-radius: 5px;
    transition: all .1s ease-out
    }

    .heading-wrapper {
        display: flex; 
        justify-content: space-between;
    }
</style>